        -ms-user-select: none;
            user-select: none;
}
.left-arrow {
    position: absolute;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: black;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
